/**
 * Hook for managing batch image processing and individual analysis
 */

import { useState, useRef } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { getProcessingStatusMessage } from '../utils/constants';

/**
 * Hook for batch processing and single image analysis
 * @param {Object} deps - Dependencies from other hooks
 * @param {Function} deps.addProcessingId - From useQueueStatus
 * @param {Function} deps.removeProcessingId - From useQueueStatus
 * @param {Function} deps.refreshAttachment - From useAttachments
 * @param {Function} deps.addCost - From useCostTracking
 * @param {Array} deps.processingIds - From useQueueStatus
 * @param {Function} deps.setAllAttachments - From useAttachments
 * @param {Array} deps.selectedIds - From useSelection
 * @param {Function} deps.clearSelection - From useSelection
 * @param {Function} deps.setAdminNotice - From MediaLibrary
 * @returns {Object} Batch processing state and handlers
 */
export const useBatchProcessing = ({
	addProcessingId,
	removeProcessingId,
	refreshAttachment,
	addCost,
	processingIds,
	setAllAttachments,
	selectedIds,
	clearSelection,
	setAdminNotice,
}) => {
	const [batchProcessing, setBatchProcessing] = useState(false);
	const [batchProgress, setBatchProgress] = useState(null);
	const cancelledRef = useRef(false);
	const [processingStatus, setProcessingStatus] = useState('');

	/**
	 * Handle single image analysis
	 * @param {number} attachmentId - Attachment ID to analyze
	 */
	const handleAnalyze = async (attachmentId) => {
		if (processingIds.includes(attachmentId)) return;

		addProcessingId(attachmentId);

		// Immediately update attachment to show "Processing..." state
		setAllAttachments(prev =>
			prev.map(att => {
				if (att.id === attachmentId) {
					return {
						...att,
						meta: {
							...att.meta,
							_ai_media_status: 'processing'
						},
						alt_text: 'Processing...',
						title: { rendered: 'Processing...' },
						caption: { rendered: 'Processing...' }
					};
				}
				return att;
			})
		);

		try {
			const analyzeResponse = await apiFetch({
				path: '/ai-media/v1/analyze',
				method: 'POST',
				data: {
					attachment_ids: [attachmentId],
					language: window.aiMediaSEO?.languages?.current || 'en',
				},
			});

			const result = analyzeResponse.results?.[0];
			console.log('[useBatchProcessing] handleAnalyze - API result:', result);
			if (result) {
				if (result.costs && result.costs.total_cost) {
					console.log('[useBatchProcessing] handleAnalyze - Adding cost:', result.costs.total_cost);
					addCost(result.costs.total_cost);
				} else {
					console.warn('[useBatchProcessing] handleAnalyze - No costs in result:', result);
				}

				await refreshAttachment(attachmentId, {
					provider: result.provider || null,
					model: result.model || null,
					score: result.score !== null && result.score !== undefined ? parseFloat(result.score) : null,
				});
			} else {
				console.warn('[useBatchProcessing] handleAnalyze - No result in response');
				await refreshAttachment(attachmentId);
			}
		} catch (err) {
			alert('Error: ' + err.message);
			await refreshAttachment(attachmentId);
		} finally {
			removeProcessingId(attachmentId);
		}
	};

	/**
	 * Handle batch analysis
	 * @param {string} mode - Mode: 'selected' or 'unprocessed'
	 */
	const handleBatchAnalyze = async (mode) => {
		if (batchProcessing) return;

		try {
			setBatchProcessing(true);
			cancelledRef.current = false;
			setAdminNotice(null);

			const batchResponse = await apiFetch({
				path: '/ai-media/v1/batch-analyze',
				method: 'POST',
				data: {
					mode: mode,
					attachment_ids: mode === 'selected' ? selectedIds : [],
					language: window.aiMediaSEO?.languages?.current || 'en',
				},
			});

			if (!batchResponse.success || batchResponse.attachment_ids.length === 0) {
				setBatchProcessing(false);
				setAdminNotice({
					type: 'warning',
					message: batchResponse.message || 'No images found to process.',
				});
				return;
			}

			const attachmentIds = batchResponse.attachment_ids;
			const language = batchResponse.language;

			setBatchProgress({
				active: true,
				current: 0,
				total: attachmentIds.length,
				success: [],
				failed: [],
				errors: {},
			});

			for (let i = 0; i < attachmentIds.length; i++) {
				if (cancelledRef.current) break;

				const attachmentId = attachmentIds[i];
				setProcessingStatus(getProcessingStatusMessage());

				const statusInterval = setInterval(() => {
					setProcessingStatus(getProcessingStatusMessage());
				}, 2000);

				try {
					const result = await apiFetch({
						path: '/ai-media/v1/process-single',
						method: 'POST',
						data: {
							attachment_id: attachmentId,
							language: language,
							auto_apply: true,
							force_reprocess: true,
						},
					});

					clearInterval(statusInterval);

					if (result.success) {
						console.log('[useBatchProcessing] handleBatchAnalyze - Success for attachment', attachmentId, 'result:', result);
						if (result.costs && result.costs.total_cost) {
							console.log('[useBatchProcessing] handleBatchAnalyze - Adding cost:', result.costs.total_cost);
							addCost(result.costs.total_cost);
						} else {
							console.warn('[useBatchProcessing] handleBatchAnalyze - No costs for attachment', attachmentId);
						}

						setBatchProgress(prev => ({
							...prev,
							current: i + 1,
							success: [...prev.success, attachmentId],
						}));
					} else {
						setBatchProgress(prev => ({
							...prev,
							current: i + 1,
							failed: [...prev.failed, attachmentId],
							errors: {
								...prev.errors,
								[attachmentId]: result.errors || ['Unknown error'],
							},
						}));
					}

					await refreshAttachment(attachmentId);

				} catch (error) {
					clearInterval(statusInterval);

					setBatchProgress(prev => ({
						...prev,
						current: i + 1,
						failed: [...prev.failed, attachmentId],
						errors: {
							...prev.errors,
							[attachmentId]: [error.message || 'Unknown error'],
						},
					}));
				}
			}

			// Show completion notice
			setBatchProgress(prev => {
				const successCount = prev.success.length;
				const failedCount = prev.failed.length;
				const wasCancelled = cancelledRef.current;

				if (wasCancelled) {
					setAdminNotice({
						type: 'warning',
						message: `Batch processing cancelled. Processed ${prev.current} of ${prev.total} images (${successCount} successful, ${failedCount} failed).`,
					});
				} else {
					if (failedCount === 0) {
						setAdminNotice({
							type: 'success',
							message: `Batch processing completed successfully! ${successCount} images processed.`,
						});
					} else if (successCount > 0) {
						setAdminNotice({
							type: 'warning',
							message: `Batch processing completed with some errors. ${successCount} successful, ${failedCount} failed.`,
						});
					} else {
						setAdminNotice({
							type: 'error',
							message: `Batch processing failed. All ${failedCount} images failed to process.`,
						});
					}
				}

				return prev;
			});

			setBatchProgress({ active: false });
			setBatchProcessing(false);
			clearSelection();

		} catch (err) {
			setAdminNotice({
				type: 'error',
				message: err.message || 'An error occurred during batch processing.',
			});
			setBatchProgress({ active: false });
		} finally {
			setBatchProcessing(false);
		}
	};

	return {
		batchProcessing,
		batchProgress,
		cancelledRef,
		processingStatus,
		handleAnalyze,
		handleBatchAnalyze,
	};
};
