/**
 * Score Cell
 * Displays AI analysis score with color coding
 */

/**
 * ScoreCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @returns {JSX.Element} Rendered score cell
 */
const ScoreCell = ({ attachment }) => {
	return (
		<td>
			{attachment.score !== null && attachment.score !== undefined ? (
				<span
					className={attachment.score >= 0.85 ? 'score-high' : attachment.score >= 0.70 ? 'score-medium' : 'score-low'}
					style={{
						display: 'inline-block',
						padding: '4px 8px',
						borderRadius: '3px',
						fontSize: '13px',
						fontWeight: '600',
						backgroundColor: attachment.score >= 0.85 ? '#d4edda' : attachment.score >= 0.70 ? '#fff3cd' : '#f8d7da',
						color: attachment.score >= 0.85 ? '#155724' : attachment.score >= 0.70 ? '#856404' : '#721c24',
					}}
				>
					{(attachment.score * 100).toFixed(0)}%
				</span>
			) : (
				<span style={{ color: '#999' }}></span>
			)}
		</td>
	);
};

export default ScoreCell;
