/**
 * Batch Processing Modal
 * Shows progress of batch AI processing with cancel option
 */

/**
 * BatchProcessingModal component
 * @param {Object} props - Component props
 * @param {Object} props.batchProgress - Batch progress data
 * @param {boolean} props.batchProgress.active - Whether batch processing is active
 * @param {number} props.batchProgress.current - Current progress count
 * @param {number} props.batchProgress.total - Total items to process
 * @param {Array} props.batchProgress.success - Array of successful items
 * @param {Array} props.batchProgress.failed - Array of failed items
 * @param {string} props.processingStatus - Current processing status message
 * @param {Function} props.onCancel - Callback when user cancels or closes modal
 * @param {Function} props.onClose - Callback to close modal when complete
 * @param {Object} props.cancelledRef - Ref to track if user cancelled
 * @returns {JSX.Element|null} Rendered modal or null if not active
 */
const BatchProcessingModal = ({ batchProgress, processingStatus, onCancel, onClose, cancelledRef }) => {
	if (!batchProgress?.active) {
		return null;
	}

	const handleButtonClick = () => {
		if (batchProgress.current === batchProgress.total) {
			// Processing complete - just close modal (notice already shown)
			onClose();
		} else {
			// Processing in progress - cancel it
			if (cancelledRef) {
				cancelledRef.current = true;
			}
			onCancel();
			// Notice will be shown when loop exits
		}
	};

	return (
		<div className="batch-processing-overlay">
			<div className="batch-processing-modal">
				<h2>AI Processing in Progress</h2>
				<p className="batch-processing-description">
					Processing images with AI. This may take several minutes. <strong>Please do not close this window</strong> until processing is complete.
				</p>

				{/* Processing Status Message */}
				{processingStatus && (
					<div className="processing-status-message">
						{processingStatus}
					</div>
				)}

				{/* Progress Bar */}
				<div className="progress-bar-container">
					<div
						className="progress-bar-fill"
						style={{
							width: `${(batchProgress.current / batchProgress.total) * 100}%`
						}}
					/>
				</div>

				{/* Progress Stats */}
				<div className="progress-stats">
					<div className="progress-percentage">
						{Math.round((batchProgress.current / batchProgress.total) * 100)}%
					</div>
					<div className="progress-text">
						{batchProgress.current} / {batchProgress.total} images
					</div>
				</div>

				{/* Batch Results */}
				<div className="batch-results">
					<div className="batch-result-item">
						<div className="result-label">Successful</div>
						<div className="success-count">{batchProgress.success.length}</div>
					</div>
					<div className="batch-result-item">
						<div className="result-label">Failed</div>
						<div className="failed-count">{batchProgress.failed.length}</div>
					</div>
				</div>

				{/* Cancel/Close Button */}
				<div className="modal-actions">
					<button
						type="button"
						onClick={handleButtonClick}
					>
						{batchProgress.current === batchProgress.total ? 'Close' : 'Cancel Processing'}
					</button>
				</div>
			</div>
		</div>
	);
};

export default BatchProcessingModal;
