/**
 * Hook for caching library pages and prefetching
 *
 * Implements LRU (Least Recently Used) cache for media library pages
 * with automatic prefetching of adjacent pages in background.
 */

import { useRef, useCallback } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Maximum number of pages to keep in cache
 * 5 pages = 100 items (at 20 per page) = ~5-15 MB RAM
 */
const MAX_CACHED_PAGES = 5;

/**
 * Cache library pages and prefetch adjacent pages
 * @returns {Object} Cache functions
 */
export const useLibraryCache = () => {
	const cache = useRef(new Map()); // Map pro cache strnek

	/**
	 * Build API URL with filters
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 * @returns {string} API URL
	 */
	const buildApiUrl = useCallback((page, perPage, filters) => {
		const params = new URLSearchParams({
			page: page,
			per_page: perPage,
			status: filters.status || 'all',
			attachment_filter: filters.attachmentFilter || 'all',
			search: filters.search || '',
		});
		return `/ai-media/v1/library?${params.toString()}`;
	}, []);

	/**
	 * Generate cache key
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 * @returns {string} Cache key
	 */
	const getCacheKey = useCallback((page, perPage, filters) => {
		return `page_${page}_${perPage}_${filters.status}_${filters.attachmentFilter}_${filters.search}`;
	}, []);

	/**
	 * Load single page (with cache check)
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 * @returns {Promise<Object>} API response
	 */
	const loadPage = useCallback(async (page, perPage, filters) => {
		const cacheKey = getCacheKey(page, perPage, filters);

		// Check cache first
		if (cache.current.has(cacheKey)) {
			console.log(`[Cache] HIT for ${cacheKey}`);
			return cache.current.get(cacheKey);
		}

		console.log(`[Cache] MISS for ${cacheKey}, fetching...`);

		// Fetch from API
		const url = buildApiUrl(page, perPage, filters);
		const response = await apiFetch({ path: url, method: 'GET' });

		// Store in cache
		cache.current.set(cacheKey, response);

		// Limit cache size (LRU - Least Recently Used)
		if (cache.current.size > MAX_CACHED_PAGES) {
			const firstKey = cache.current.keys().next().value;
			cache.current.delete(firstKey);
			console.log(`[Cache] Evicted ${firstKey} (size limit reached)`);
		}

		return response;
	}, [buildApiUrl, getCacheKey]);

	/**
	 * Prefetch adjacent pages in background
	 * @param {number} currentPage - Current page number
	 * @param {number} totalPages - Total pages available
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 */
	const prefetchAdjacentPages = useCallback((currentPage, totalPages, perPage, filters) => {
		const toLoad = [];

		// Previous page
		if (currentPage > 1) {
			const prevKey = getCacheKey(currentPage - 1, perPage, filters);
			if (!cache.current.has(prevKey)) {
				toLoad.push(loadPage(currentPage - 1, perPage, filters));
			}
		}

		// Next page
		if (currentPage < totalPages) {
			const nextKey = getCacheKey(currentPage + 1, perPage, filters);
			if (!cache.current.has(nextKey)) {
				toLoad.push(loadPage(currentPage + 1, perPage, filters));
			}
		}

		// Next +2 page (lower priority)
		if (currentPage + 2 <= totalPages) {
			const next2Key = getCacheKey(currentPage + 2, perPage, filters);
			if (!cache.current.has(next2Key)) {
				toLoad.push(loadPage(currentPage + 2, perPage, filters));
			}
		}

		// Run in background (don't block UI)
		if (toLoad.length > 0) {
			console.log(`[Prefetch] Loading ${toLoad.length} pages in background`);
			Promise.all(toLoad).catch(err => {
				console.warn('[Prefetch] Failed:', err);
			});
		}
	}, [getCacheKey, loadPage]);

	/**
	 * Clear all cache (on filter change)
	 */
	const clearCache = useCallback(() => {
		console.log('[Cache] Clearing all cache');
		cache.current.clear();
	}, []);

	/**
	 * Get current cache size
	 * @returns {number} Number of cached pages
	 */
	const getCacheSize = useCallback(() => {
		return cache.current.size;
	}, []);

	return {
		loadPage,
		prefetchAdjacentPages,
		clearCache,
		getCacheSize,
	};
};
