/**
 * Bulk Actions Component
 * Bulk actions toolbar with analyze and delete buttons, plus cost display
 */

import { Button } from '@wordpress/components';

/**
 * BulkActions component
 * @param {Object} props - Component props
 * @param {Array} props.selectedIds - Array of selected attachment IDs
 * @param {boolean} props.batchProcessing - Whether batch processing is in progress
 * @param {Function} props.onBatchAnalyze - Callback for batch analyze with mode ('selected', 'missing_metadata', 'all')
 * @param {Function} props.onBulkDelete - Callback for bulk delete
 * @param {number} props.totalCost - Total cost
 * @param {boolean} props.loadingCosts - Whether costs are loading
 * @param {Function} props.onShowCostModal - Callback to show cost modal
 * @param {Function} props.onLoadCostStats - Callback to load cost stats
 * @returns {JSX.Element} Rendered bulk actions toolbar
 */
const BulkActions = ({
	selectedIds,
	batchProcessing,
	onBatchAnalyze,
	onBulkDelete,
	totalCost,
	loadingCosts,
	onShowCostModal,
	onLoadCostStats,
}) => {
	return (
		<div className="ai-media-library-bulk-actions">
			{selectedIds.length > 0 ? (
				<>
					<Button
						variant="primary"
						onClick={() => onBatchAnalyze('selected')}
						disabled={batchProcessing}
						isBusy={batchProcessing}
					>
						{batchProcessing ? 'Processing...' : `Analyze Selected (${selectedIds.length})`}
					</Button>
					<Button
						variant="secondary"
						onClick={onBulkDelete}
						disabled={batchProcessing}
						style={{
							color: '#b32d2e',
							borderColor: '#b32d2e',
							marginLeft: '8px'
						}}
					>
						Bulk Delete ({selectedIds.length})
					</Button>
				</>
			) : (
				<>
					<Button
						variant="secondary"
						onClick={() => onBatchAnalyze('missing_metadata')}
						disabled={batchProcessing}
					>
						Analyze Missing Metadata
					</Button>
					<Button
						variant="secondary"
						onClick={() => onBatchAnalyze('all')}
						disabled={batchProcessing}
					>
						Analyze All Images
					</Button>
				</>
			)}

			{/* Cost Display */}
			<button
				type="button"
				onClick={() => {
					onLoadCostStats();
					onShowCostModal();
				}}
				style={{
					marginLeft: 'auto',
					padding: '8px 16px',
					background: '#f0f6fc',
					border: '1px solid #2271b1',
					borderRadius: '4px',
					display: 'flex',
					alignItems: 'center',
					gap: '8px',
					cursor: 'pointer',
					transition: 'all 0.2s ease',
				}}
				onMouseEnter={(e) => {
					e.currentTarget.style.background = '#e5f2fa';
					e.currentTarget.style.borderColor = '#135e96';
				}}
				onMouseLeave={(e) => {
					e.currentTarget.style.background = '#f0f6fc';
					e.currentTarget.style.borderColor = '#2271b1';
				}}
			>
				<span
					className="dashicons dashicons-money-alt"
					style={{
						color: '#2271b1',
						fontSize: '18px',
						width: '18px',
						height: '18px',
					}}
				></span>
				<div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
					<span
						style={{
							fontSize: '11px',
							color: '#646970',
							fontWeight: '500',
							textTransform: 'uppercase',
							letterSpacing: '0.5px',
							lineHeight: '1.2',
						}}
					>
						Total Cost
					</span>
					<span
						style={{
							fontSize: '16px',
							fontWeight: '600',
							color: '#1d2327',
							lineHeight: '1.2',
						}}
					>
						{loadingCosts ? '...' : `$${totalCost.toFixed(4)}`}
					</span>
				</div>
			</button>
		</div>
	);
};

export default BulkActions;
