/**
 * Attach to Post Modal
 * Allows searching for and attaching media to existing posts/pages
 */

import { Modal, Button } from '@wordpress/components';
import { formatDate } from '../../utils/formatters';

/**
 * AttachModal component
 * @param {Object} props - Component props
 * @param {boolean} props.isOpen - Whether modal is open
 * @param {Function} props.onClose - Callback to close modal
 * @param {string} props.searchQuery - Current search query
 * @param {Function} props.onSearchChange - Callback when search query changes
 * @param {Function} props.onSearch - Callback to perform search
 * @param {boolean} props.isSearching - Whether search is in progress
 * @param {Array} props.searchResults - Array of search results
 * @param {number|null} props.selectedId - Currently selected post ID
 * @param {Function} props.onSelectId - Callback when post is selected
 * @param {Function} props.onSubmit - Callback when Select button is clicked
 * @returns {JSX.Element|null} Rendered modal or null if not open
 */
const AttachModal = ({
	isOpen,
	onClose,
	searchQuery,
	onSearchChange,
	onSearch,
	isSearching,
	searchResults,
	selectedId,
	onSelectId,
	onSubmit,
}) => {
	if (!isOpen) {
		return null;
	}

	return (
		<Modal
			title="Attach to existing content"
			onRequestClose={onClose}
			className="ai-media-attach-modal"
			style={{ maxWidth: '720px' }}
		>
			<div className="find-box-search" style={{ padding: '20px 20px 0', borderBottom: '1px solid #dcdcde', marginBottom: '16px' }}>
				<input
					type="text"
					value={searchQuery}
					onChange={(e) => onSearchChange(e.target.value)}
					onKeyDown={(e) => e.key === 'Enter' && onSearch()}
					placeholder="Search"
					style={{
						width: 'calc(100% - 90px)',
						padding: '6px 12px',
						fontSize: '14px',
						border: '1px solid #8c8f94',
						borderRadius: '3px',
						marginRight: '8px',
					}}
				/>
				<Button
					variant="secondary"
					onClick={onSearch}
					isBusy={isSearching}
					disabled={isSearching}
				>
					{isSearching ? 'Searching...' : 'Search'}
				</Button>
			</div>

			<div className="find-box-response" style={{ maxHeight: '400px', overflowY: 'auto', padding: '0 20px' }}>
				{searchResults.length === 0 ? (
					<p style={{ textAlign: 'center', color: '#646970', padding: '40px 20px' }}>
						{isSearching ? 'Searching...' : 'No results found. Try a different search term.'}
					</p>
				) : (
					<table className="widefat" style={{ marginBottom: '0' }}>
						<thead>
							<tr>
								<th style={{ width: '40px' }}></th>
								<th>Title</th>
								<th style={{ whiteSpace: 'nowrap' }}>Type</th>
								<th style={{ whiteSpace: 'nowrap' }}>Date</th>
								<th style={{ whiteSpace: 'nowrap' }}>Status</th>
							</tr>
						</thead>
						<tbody>
							{searchResults.map((result, index) => (
								<tr key={result.id} className={index % 2 === 0 ? '' : 'alternate'}>
									<td style={{ textAlign: 'center' }}>
										<input
											type="radio"
											name="found_post_id"
											value={result.id}
											checked={selectedId === result.id}
											onChange={() => onSelectId(result.id)}
											id={`found-${result.id}`}
										/>
									</td>
									<td>
										<label htmlFor={`found-${result.id}`} style={{ cursor: 'pointer' }}>
											{result.title}
										</label>
									</td>
									<td style={{ whiteSpace: 'nowrap' }}>{result.type}</td>
									<td style={{ whiteSpace: 'nowrap' }}>{formatDate(result.date)}</td>
									<td style={{ whiteSpace: 'nowrap' }}>{result.status}</td>
								</tr>
							))}
						</tbody>
					</table>
				)}
			</div>

			<div className="find-box-buttons" style={{ padding: '16px 20px', borderTop: '1px solid #dcdcde', display: 'flex', justifyContent: 'flex-end', gap: '8px' }}>
				<Button variant="secondary" onClick={onClose}>
					Close
				</Button>
				<Button
					variant="primary"
					onClick={onSubmit}
					disabled={!selectedId}
				>
					Select
				</Button>
			</div>
		</Modal>
	);
};

export default AttachModal;
