/**
 * Hook for managing library data with server-side pagination
 *
 * Uses the new /ai-media/v1/library endpoint with built-in caching and prefetching.
 */

import { useState, useCallback } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { useLibraryCache } from './useLibraryCache';
import { stripHtml } from '../utils/formatters';

/**
 * Manage library data with server-side operations
 * @returns {Object} Library state and utilities
 */
export const useAttachments = () => {
	const [items, setItems] = useState([]);
	const [totalItems, setTotalItems] = useState(0);
	const [totalPages, setTotalPages] = useState(0);
	const [loading, setLoading] = useState(true);
	const [error, setError] = useState(null);

	const { loadPage, prefetchAdjacentPages, clearCache } = useLibraryCache();

	/**
	 * Load library page with filters
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 */
	const loadLibrary = useCallback(async (page, perPage, filters) => {
		console.log('[useAttachments] Loading page', page, 'with filters:', filters);

		try {
			setLoading(true);
			setError(null);

			// Load from cache or API
			const response = await loadPage(page, perPage, filters);

			console.log('[useAttachments] Loaded:', response.items.length, 'items, total:', response.total);

			// Update state
			setItems(response.items);
			setTotalItems(response.total);
			setTotalPages(response.total_pages);

			// Prefetch adjacent pages in background
			prefetchAdjacentPages(page, response.total_pages, perPage, filters);

			setLoading(false);
		} catch (err) {
			console.error('[useAttachments] Error loading library:', err);
			setError(err.message);
			setLoading(false);
		}
	}, [loadPage, prefetchAdjacentPages]);

	/**
	 * Refresh single attachment after update
	 * @param {number} attachmentId - Attachment ID to refresh
	 * @param {Object} jobData - Optional job data (provider, model, score)
	 */
	const refreshAttachment = useCallback(async (attachmentId, jobData = null) => {
		try {
			const response = await apiFetch({
				path: `/wp/v2/media/${attachmentId}`,
				method: 'GET',
			});

			// Fetch job data if not provided
			if (!jobData) {
				jobData = { provider: null, model: null, score: null };
				try {
					const jobsResponse = await apiFetch({
						path: `/ai-media/v1/jobs?attachment_ids=${attachmentId}`,
						method: 'GET',
					});

					const latestJob = jobsResponse.jobs
						?.filter(job => job.attachment_id == attachmentId)
						.sort((a, b) => new Date(b.created_at) - new Date(a.created_at))[0];

					if (latestJob) {
						jobData = {
							provider: latestJob.provider || null,
							model: latestJob.model || null,
							score: latestJob.score !== null ? parseFloat(latestJob.score) : null,
						};
					}
				} catch (jobErr) {
					console.warn('[useAttachments] Failed to load job data:', jobErr);
				}
			}

			// Update in current items (optimistic update)
			setItems(prev =>
				prev.map(item => item.id === attachmentId ? {
					...response,
					provider: jobData.provider,
					model: jobData.model,
					score: jobData.score,
				} : item)
			);

			// Note: We don't clear cache here - optimistic update is enough
			// Cache will be invalidated on page change or filter change

		} catch (err) {
			console.error('[useAttachments] Error refreshing attachment:', err);
		}
	}, []);

	/**
	 * Attach media to a parent post/page
	 * @param {number} attachmentId - Attachment ID
	 * @param {number} parentId - Parent post/page ID
	 */
	const attachToParent = useCallback(async (attachmentId, parentId) => {
		await apiFetch({
			path: `/wp/v2/media/${attachmentId}`,
			method: 'POST',
			data: {
				post: parentId,
			},
		});

		// Fetch parent post details
		let parentPostData = null;
		try {
			const response = await apiFetch({
				path: `/wp/v2/posts/${parentId}`,
				method: 'GET',
			});

			parentPostData = {
				id: response.id,
				title: stripHtml(response.title?.rendered) || 'Untitled',
				type: response.type || 'post',
			};
		} catch (err) {
			// Try as a page
			try {
				const response = await apiFetch({
					path: `/wp/v2/pages/${parentId}`,
					method: 'GET',
				});

				parentPostData = {
					id: response.id,
					title: stripHtml(response.title?.rendered) || 'Untitled',
					type: response.type || 'page',
				};
			} catch (pageErr) {
				// Fallback
				parentPostData = {
					id: parentId,
					title: '(Unknown)',
					type: 'unknown',
				};
			}
		}

		// Update local state (optimistic update)
		setItems(prev =>
			prev.map(item => item.id === attachmentId ? {
				...item,
				post: parentId,
				parent: parentId,
				parent_info: parentPostData,
			} : item)
		);
	}, []);

	/**
	 * Detach media from its parent post/page
	 * @param {number} attachmentId - Attachment ID
	 */
	const handleDetach = useCallback(async (attachmentId) => {
		if (!confirm('Detach this image from its post/page?')) {
			return;
		}

		try {
			await apiFetch({
				path: `/wp/v2/media/${attachmentId}`,
				method: 'POST',
				data: {
					post: 0,
				},
			});

			// Update local state (optimistic update)
			setItems(prev =>
				prev.map(item => item.id === attachmentId ? {
					...item,
					post: 0,
					parent: 0,
					parent_info: null,
				} : item)
			);
		} catch (err) {
			alert('Error detaching image: ' + err.message);
		}
	}, []);

	/**
	 * Invalidate cache (call when filters change)
	 */
	const invalidateCache = useCallback(() => {
		console.log('[useAttachments] Invalidating cache');
		clearCache();
	}, [clearCache]);

	return {
		// Data
		items,
		totalItems,
		totalPages,
		loading,
		error,

		// Functions
		loadLibrary,
		refreshAttachment,
		attachToParent,
		handleDetach,
		invalidateCache,

		// For compatibility (if other hooks use these)
		setItems,
		setLoading,
		setError,
	};
};
